.goal-goals-list-wrap .goals-list-box .goals-list-button {
  background-color: #f5f5f5;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
